import React, { useEffect } from 'react'
import Typed from 'typed.js'
import avatar from '../../images/avatar.svg'

import './About.scss'

const options = {
  strings: [
    'Shuyoou',
  ],
  typeSpeed: 60,
  showCursor: false,
};

export const About = () => {
  useEffect(() => {
    const typed = new Typed('.typed-container', options)
    return () => {
      typed.destroy()
    }
  }, [])

  return (
    <div className='About'>
      <div style={{backgroundImage: `url(${avatar})`}} className='avatar' />
      <div className='typed-name'></div>
      <div className='typed-container'></div>
    </div>
  )
}
